import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import loadable from '@loadable/component';
import { createTheme, ThemeProvider } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import SectionMenu from '../components/sections/menu';
import SectionMainvisual from '../components/sections/mainvisual';
import '../index.css';

const SectionAbout = loadable(() => import('../components/sections/about'));
const SectionFacility = loadable(() =>
  import('../components/sections/facility')
);
const SectionPricing = loadable(() => import('../components/sections/pricing'));
const SectionBooking = loadable(() => import('../components/sections/booking'));
// const SectionTrainer = loadable(() => import('../components/sections/trainer'));
const SectionAccess = loadable(() => import('../components/sections/access'));
const SectionContact = loadable(() => import('../components/sections/contact'));

// @TODO from gatsby-config
const title = 'addict gym';
const url = 'https://addict-gym.com/';
const description =
  'addict gym';
const gaTrackingId = 'UA-204339469-1';

const theme = createTheme({
  default: '#ffffff',
  color: '#ffffff',
  root: {
    dark: '#ffffff',
    color: '#ffffff'
  },
  palette: {
    default: {
      color: '#ffffff'
    },
    primary: {
      main: '#b28223'
    },
    text: {
      primary: '#00ff00',
      dark: '#ff00ff',
      secondary: '#ff0000'
    }
  },
  typography: {
    fontSize: 12,
    fontFamily: ['YuMincho', 'serif'].join(','),
    h4: {
      fontSize: 32,
      letterSpacing: '0.1rem'
    },
    h5: {
      fontSize: 28
    }
  }
});

const IndexPage = () => {
  return (
    <ThemeProvider theme={theme}>
      <Helmet>
        <title>{title}</title>
        <meta name='description' content={description} />
        <meta property='og:title' content={title} />
        <meta property='og:type' content='website' />
        <meta property='og:url' content={url} />
        <meta property='og:description' content={description} />
        <meta property='og:image' content={`${url}ogp.png`} />
        <meta property='og:image:secure_url' content={`${url}ogp.png`} />
        <meta property='og:image:type' content='image/png' />
        <meta property='og:image:width' content={1200} />
        <meta property='og:image:height' content={630} />
        <meta property='og:locale' content='ja_JP' />
        <script
          async
          src={`https://www.googletagmanager.com/gtag/js?id=${gaTrackingId}`}
        ></script>
        <script>{`
window.dataLayer = window.dataLayer || [];
function gtag(){dataLayer.push(arguments);}
gtag('js', new Date());
gtag('config', '${gaTrackingId}');
      `}</script>
      </Helmet>

      <SectionMenu />
      <SectionMainvisual />
      <SectionAbout />

      <Container maxWidth={false}>
        <Typography
          sx={{
            display: {
              xs: 'none',
              md: 'block'
            },
            marginTop: '-10vw',
            marginBottom: '-6vw',
            fontSize: '18vw',
            fontWeight: 800,
            textAlign: 'center',
            color: '#f7f3e9',
            pointerEvents: 'none'
          }}
        >
          addict gym
        </Typography>
      </Container>

      <SectionFacility />
      <SectionPricing />
      <SectionBooking />
      <SectionAccess />
      <SectionContact />
    </ThemeProvider>
  );
};

export default IndexPage;
